import React, { useState, useEffect } from 'react';

function Title() {
    const [title, setTitle] = useState(null);

    useEffect(() => {
        let subscription;
        if (window.CDP && window.parent !== window) {
            window.CDP.register()
                .then(() => window.CDP.state('title').subscribe((title) => setTitle(title)))
                .then((sub) => { subscription = sub; });
        } else {
            setTitle('Unit One');
        }

        return () => {
            if (subscription) {
                subscription.unsubscribe();
            }
        };
    }, []);

    return <h1>{title ? title : null}</h1>;
}

export default Title;
